export default {

	copyText(text) {
		uni.setClipboardData({
			data: text
		});
	},
	rmbDaXieZhuanHuan(n) {
		var fraction = ['', ''];
		var digit = ['', '', '', '', '', '', '', '', '', ''];
		var unit = [
			['', '', ''],
			['', '', '', '']
		];
		var head = n < 0 ? '' : '';
		n = Math.abs(n);

		var s = '';
		//
		for (var i = 0; i < fraction.length; i++) {
			s += (digit[Math.floor(n * 10 * Math.pow(10, i)) % 10] + fraction[i]).replace(/./, '');
		}

		s = s || ''; //s
		n = Math.floor(n);
		for (var i = 0; i < unit[0].length && n > 0; i++) {
			var p = '';
			for (var j = 0; j < unit[1].length && n > 0; j++) {
				p = digit[n % 10] + unit[1][j] + p;
				n = Math.floor(n / 10);
			}
			s = p.replace(/(.)*$/, '').replace(/^$/, '') + unit[0][i] + s;
		}
		return head + s.replace(/(.)*/, '').replace(/(.)+/g, '').replace(/^$/, '');
	},
	xuankunicheng(){
		[ {
		    class: "",
		    datalist: [ "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", " ", "", "", "", "", "", "", "", "", "" ]
		}, {
		    class: "",
		    datalist: [ "", "", ".dear", "ve", "ve", " ", "", "", "Alone", "", "unique", "r`", ".r", " ", "", "", "", "" ]
		}, {
		    class: "",
		    datalist: [ "", "Fo", "kiss", "Win", "A", "", "TK", "super", "Spurt", "Sky", "Star", "", "", "S", "Culb", "__Home", "", "v1p", "", "TK" ]
		}, {
		    class: "",
		    datalist: [ "`", ";", "?", "/.", "", "", "", "", "", "_~", "", "", "s:", "", "", "oo-~", "o:", "/", "", "", "", "^." ]
		} ]
	}
}
